.header{text-align:center;padding:40px 20px;margin-bottom:30px;background:rgba(0,0,0,.4);border-radius:15px;box-shadow:0 10px 30px rgba(0,0,0,.3);backdrop-filter:blur(5px);border:1px solid hsla(0,0%,100%,.1);position:relative;overflow:hidden}.header::before{content:"";position:absolute;top:-50%;left:-50%;width:200%;height:200%;background:radial-gradient(circle, rgba(255, 255, 255, 0.1) 0%, rgba(255, 255, 255, 0) 70%);z-index:-1}.header h1{font-size:3rem;margin-bottom:15px;color:#fff;text-shadow:0 0 10px hsla(0,0%,100%,.5)}.header p{font-size:1.4rem;max-width:800px;margin:0 auto;color:#e0e0e0;line-height:1.6}.container{display:flex;flex-direction:column;gap:30px;max-width:1200px;margin:0 auto}.element-section{background:hsla(0,0%,100%,.85);border-radius:15px;padding:30px;box-shadow:0 10px 30px rgba(0,0,0,.4);backdrop-filter:blur(5px);border:1px solid hsla(0,0%,100%,.1);transition:transform .3s ease,box-shadow .3s ease}.element-section:hover{transform:translateY(-5px);box-shadow:0 15px 35px rgba(0,0,0,.5)}.element-header{display:flex;align-items:center;gap:15px;margin-bottom:25px;padding-bottom:15px;border-bottom:2px solid}.air .element-header{border-bottom-color:rgba(100,200,255,.5)}.fire .element-header{border-bottom-color:rgba(255,100,100,.5)}.earth .element-header{border-bottom-color:rgba(165,113,78,.5)}.water .element-header{border-bottom-color:rgba(100,150,255,.5)}.habits .element-header{border-bottom-color:rgba(180,100,255,.5)}.element-header h2{font-size:1.8rem}.air .element-header h2{color:#64c8ff}.fire .element-header h2{color:#ff6464}.earth .element-header h2{color:#a5714e}.water .element-header h2{color:#6496ff}.habits .element-header h2{color:#b464ff}.element-header i{font-size:2.5rem}.air .element-header i{color:#64c8ff}.fire .element-header i{color:#ff6464}.earth .element-header i{color:#a5714e}.water .element-header i{color:#6496ff}.habits .element-header i{color:#b464ff}.principle-list{list-style-type:none;padding-left:20px}.principle-list li{margin-b