n:hover{transform:translateY(-2px);box-shadow:0 5px 15px rgba(0,0,0,.3)}.import-export-buttons .import-btn{background:linear-gradient(135deg, #667eea 0%, #764ba2 100%);color:#fff}.import-export-buttons .import-btn:hover{background:linear-gradient(135deg, #764ba2 0%, #667eea 100%)}.import-export-buttons .export-btn{background:linear-gradient(135deg, #f093fb 0%, #f5576c 100%);color:#fff}.import-export-buttons .export-btn:hover{background:linear-gradient(135deg, #f5576c 0%, #f093fb 100%)}.header{text-align:center;padding:40px 20px;margin-bottom:30px;background:rgba(0,0,0,.4);border-radius:15px;box-shadow:0 10px 30px rgba(0,0,0,.3);backdrop-filter:blur(5px);border:1px solid hsla(0,0%,100%,.1);position:relative;overflow:hidden}